import { createStore } from "vuex";
import { request } from "@/http.js";
import { debounce } from "lodash";
import API_URLS from "@/api";

const debouncedChangeProfile = debounce((store, profile, url) => {
  store.dispatch("setProfile", { profile, url });
}, 1000);

export default createStore({
  state() {
    document.getElementsByTagName("html")[0].style.fontSize =
      window.backData?.userInfo?.profile?.fontSize ?? "16px";
    return {
      userInfo: window.backData?.userInfo ?? null,
      appTitle: process.env.VUE_APP_TITLE,
    };
  },
  getters: {
    profile(state) {
      return state.userInfo?.profile ?? {};
    },
    isAuthenticated(state) {
      return !!state.userInfo;
    },
    isAdmin(state) {
      return (
        state.userInfo?.groups?.includes("Администраторы") ||
        state.userInfo?.is_superuser
      );
    },
    isEditor(state) {
      return (
        ["Администраторы", "Редакторы"].some((i) =>
          state.userInfo?.groups?.includes(i)
        ) || state.userInfo?.is_superuser
      );
    },
    userInfo(state) {
      return state.userInfo ?? null;
    },
    userName(state) {
      return state.userInfo?.full_name ?? "Гость";
    },
    userUnits(state) {
      return state.userInfo?.info ? state.userInfo?.info?.split("#") : [];
    },
    appTitle(state) {
      return state.appTitle;
    },
    appSettings(state) {
      return state.settings;
    },
  },
  mutations: {
    setFontSize(state, fontSize) {
      if (state.userInfo?.profile?.fontSize === fontSize) return;
      state.userInfo.profile.fontSize = fontSize;
      document.getElementsByTagName("html")[0].style.fontSize = fontSize;
      if (state.userInfo?.url) {
        debouncedChangeProfile(
          this,
          JSON.parse(JSON.stringify(state.userInfo.profile)),
          state.userInfo?.url
        );
      }
    },
    updateUserInfo(state, userData) {
      if (userData?.url) {
        state.userInfo = userData?.email ? userData : null;
        document.getElementsByTagName("html")[0].style.fontSize =
          userData?.profile?.fontSize ?? "16px";
      }
    },
  },
  actions: {
    async getUserInfo({ state, commit }) {
      const response = await request(API_URLS.users.info);
      if (response.email) {
        if (state.userInfo?.email !== response.email) {
          commit("updateUserInfo", response);
        }
      } else {
        window.location.href = `admin/login/?next=${window.location.origin}`;
      }
    },
    async setProfile(_, { profile, url }) {
      await request(`${url}update_profile/`, "PATCH", profile);
    },
  },
});
