<template>
  <div class="sidebar-wrapper" :class="{ full: full }">
    <div v-for="(items, position) in fullSidebarItems" :key="position">
      <UKKSidebarItem
        v-for="(item, index) in items"
        :key="index"
        :href="item.href"
        :to="item.to"
        :label="item.label"
        @click="item.callback"
        :full="full"
        :image="item.image"
        :style="item.style"
      />
      <slot :name="position" :full="full"></slot>
    </div>
  </div>
</template>

<script>
import UKKSidebarItem from "@/shared/components/UKKSidebarItem.vue";
// images
import arrowImage from "@/assets/icons/sidebar/arrow.svg";

export default {
  name: "UKKSidebar",
  components: { UKKSidebarItem },
  props: ["sidebarItems"],
  data() {
    return {
      full: false,
    };
  },
  computed: {
    fullSidebarItems() {
      const items = JSON.parse(JSON.stringify(this.sidebarItems));
      if (!items.bottom) items.bottom = [];
      items.bottom = [
        {
          callback: this.triggerFull,
          image: arrowImage,
          style: `--image-transform: ${
            this.full ? "scale(-1, 1)" : "scale(1, 1)"
          }; margin-bottom: 50px`,
        },
      ].concat(items.bottom);
      return items;
    },
  },
  methods: {
    triggerFull() {
      this.full = !this.full;
    },
  },
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.sidebar-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 101;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: minmax(150px, auto) 1fr minmax(80px, auto);
  align-items: center;
  width: var(--sidebar-close-width);
  padding: var(--sidebar-padding);
  background-color: #edf5ff;
  transition: width 0.5s ease, padding-left 0.5s ease;
}
.full {
  overflow: hidden;
  width: var(--sidebar-open-width);
  padding-left: 1rem;
}
</style>
