<template>
  <div id="app-root">
    <UKKSidebar style="grid-area: sidebar" :sidebarItems="sidebarItems" />
    <UKKHeader style="grid-area: header" />
    <div style="grid-area: body" id="main-content-container">
      <router-view />
    </div>
  </div>
</template>

<script>
import UKKHeader from "@/shared/components/UKKHeader.vue";
import UKKSidebar from "@/shared/components/UKKSidebar.vue";
import { mapGetters } from "vuex";
// images
import portalImage from "@/assets/icons/sidebar/portal.svg";
import profileImage from "@/assets/icons/sidebar/profile.svg";
import exitImage from "@/assets/icons/sidebar/exit.svg";
import mailImage from "@/assets/icons/sidebar/mail.svg";
import mainImage from "@/assets/icons/sidebar/main.svg";
import settingsImage from "@/assets/icons/sidebar/settings.svg";
import store from "@/store";

export default {
  name: "UKKApp",
  components: {
    UKKHeader,
    UKKSidebar,
  },
  data() {
    return {};
  },
  async beforeMount() {
    await store.dispatch("getUserInfo");
  },
  computed: {
    ...mapGetters(["isAuthenticated", "isAdmin"]),
    sidebarItems() {
      const profileRoute = this.getRoute("Профиль");
      const items = {
        top: [
          {
            href: this.portalUrl,
            label: "Портал УКК",
            image: portalImage,
          },
        ],
        middle: [
          {
            to: "/",
            label: "Главная",
            image: mainImage,
          },
        ],
        bottom: [
          {
            href: "mailto:OIP_NT_TEAM@vtb.ru?subject=[calendar] Обратная связь",
            label: "Обратная связь",
            image: mailImage,
          },
        ],
      };
      if (this.isAuthenticated) {
        items.top.push({
          to: profileRoute.href,
          label: profileRoute.name,
          image: profileImage,
        });
        items.top.push({
          href: this.logoutUrl,
          label: "Выйти",
          image: exitImage,
        });
      } else
        items.top.push({
          href: this.loginUrl,
          label: "Войти",
          image: exitImage,
        });
      if (this.isAdmin)
        items.middle.push({
          href: this.adminUrl,
          label: "Управление",
          image: settingsImage,
        });
      return items;
    },
    portalUrl() {
      return window.location.origin.replace(
        `${window.location.hostname.split(".")[0]}.`,
        ""
      );
    },
    adminUrl() {
      return `${window.location.origin}/admin/`;
    },
    loginUrl() {
      return `${window.location.origin}/admin/login/?next=${window.location.href}`;
    },
    logoutUrl() {
      return `${window.location.origin}/admin/logout/?next=${window.location.href}`;
    },
  },
  methods: {
    getRoute: function (name) {
      return this.$router.resolve({
        name: name,
      });
    },
  },
};
</script>

