<template>
  <div class="d-flex ukk-blue-gradient">
    <div class="header-title">
      <h2 class>
        {{
          this.$route.name
            ? `${appTitle} &#8250; ${this.$route.name}`
            : `${appTitle}`
        }}
      </h2>
    </div>
    <v-spacer />
    <div class="header-logo">
      <img :src="require('@/assets/logo.svg')" alt="" />
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";

export default {
  name: "UKKHeader",
  emits: ["resize"],
  data() {
    return {
      height: 0,
    };
  },
  updated() {
    if (this.height !== this.$el.clientHeight) {
      this.height = this.$el.clientHeight;
      this.$emit(
        "resize",
        `${
          Math.round(
            this.$el.clientHeight /
              parseFloat(getComputedStyle(document.documentElement).fontSize)
          ) + 1
        }rem`
      );
    }
  },
  computed: {
    ...mapGetters(["appTitle"]),
  },
};
</script>

<style lang="scss">
.ukk-blue-gradient {
  $logoWidth: 4rem;
  background: linear-gradient(180deg, #1a47c0, #3176dd);
  display: grid;
  grid-template-columns: 1fr $logoWidth;
  padding: 0.6rem 1rem 0.5rem 2rem;

  .header-logo {
    img {
      width: $logoWidth;
    }
  }
  .header-title {
    h2 {
      color: #fff;
      font-weight: 600;
      font-size: 1.3rem !important;
      word-break: break-word;
    }
  }
}
</style>
